
.neurogame {
  width: 100vw;
  height: 100vh;
}
.game-info {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  height: 40px;
  background: #444;
}
.game-info span {
  -webkit-box-flex: 1;
      -ms-flex-positive: 1;
          flex-grow: 1;
  text-align: center;
  line-height: 40px;
  font-family: sans-serif;
  font-size: 12px;
  color: white;
  font-weight: bold;
}



